@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap");

:root {
   --primary-color: #2ecc71;
   --secondary-color: #16a085;
   --hero-background: #2d3436;
}

* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: "Noto Sans JP", sans-serif;
}

body {
   background-color: #636e72;
   color: white;
}

.container {
   width: 90%;
   margin: 0 auto;
}

.wrapper {
   position: relative;
   margin: 50px 0 45px;
}

.recent-project-section {
   padding-bottom: 3rem;
}

.recent-projects {
   display: flex;
   flex-wrap: wrap;
   justify-content: space-between;
   padding: 1rem;
   text-align: center;
}

.section-header {
   text-align: center;
   font-size: 2.1rem;
   margin-top: 30px;
   margin-bottom: 10px;
   letter-spacing: 2px;
   text-transform: uppercase;
}

.overlay {
   background-color: black;
   opacity: 0.7;
}

/* Projects page */

.projects-page {
   display: flex;
   flex-wrap: wrap;
   padding: 3rem;
   justify-content: center;
   width: 100%;
}

footer {
   width: 100%;
   background-color: #2c3e50;
   display: flex;
   align-items: center;
   padding: 4rem;
}

.social {
   width: 300px;
}

.social i {
   font-size: 3rem;
}

.social a {
   color: white;
}

/* Project Page */

.project-page {
}

.project-page-image {
   width: 800px;

   box-shadow: 0px 0px 10px black;
}

.project-page-image img {
   width: 800px;
}

.notfound {
   margin: 0 auto;
   margin-top: 300px;
}

.technical-section {
   width: 100%;
   height: 600px;
   padding: 2rem;
   background-color: var(--hero-background);
   color: white;
}

.technical-section ul {
   display: flex;
   align-items: center;
   justify-content: center;
   padding: 1rem;
}

.languages h2 {
   letter-spacing: 2px;
   font-size: 2rem;
}

.languages ul {
   display: block;
}

.languages ul li {
   list-style-type: none;
   margin-bottom: 3px;
}

.about-container {
   width: 100%;
   padding: 3rem;
   height: 400px;
   display: flex;
   justify-content: center;
   background: linear-gradient(180deg, #2d3436 0%, #2d3436 35%, #636e72 100%);
}

.about-container h2 {
   font-size: 2.8rem;
   text-align: center;
   margin-bottom: 20px;
   letter-spacing: 3px;
   text-transform: uppercase;
}

.about-container p {
   font-size: 1.1rem;
   letter-spacing: 3px;
}

.about-content {
   width: 1200px;
}
