@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
:root {
   --primary-color: #2ecc71;
   --secondary-color: #16a085;
   --hero-background: #2d3436;
}

* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: "Noto Sans JP", sans-serif;
}

body {
   background-color: #636e72;
   color: white;
}

.container {
   width: 90%;
   margin: 0 auto;
}

.wrapper {
   position: relative;
   margin: 50px 0 45px;
}

.recent-project-section {
   padding-bottom: 3rem;
}

.recent-projects {
   display: flex;
   flex-wrap: wrap;
   justify-content: space-between;
   padding: 1rem;
   text-align: center;
}

.section-header {
   text-align: center;
   font-size: 2.1rem;
   margin-top: 30px;
   margin-bottom: 10px;
   letter-spacing: 2px;
   text-transform: uppercase;
}

.overlay {
   background-color: black;
   opacity: 0.7;
}

/* Projects page */

.projects-page {
   display: flex;
   flex-wrap: wrap;
   padding: 3rem;
   justify-content: center;
   width: 100%;
}

footer {
   width: 100%;
   background-color: #2c3e50;
   display: flex;
   align-items: center;
   padding: 4rem;
}

.social {
   width: 300px;
}

.social i {
   font-size: 3rem;
}

.social a {
   color: white;
}

/* Project Page */

.project-page {
}

.project-page-image {
   width: 800px;

   box-shadow: 0px 0px 10px black;
}

.project-page-image img {
   width: 800px;
}

.notfound {
   margin: 0 auto;
   margin-top: 300px;
}

.technical-section {
   width: 100%;
   height: 600px;
   padding: 2rem;
   background-color: #2d3436;
   background-color: var(--hero-background);
   color: white;
}

.technical-section ul {
   display: flex;
   align-items: center;
   justify-content: center;
   padding: 1rem;
}

.languages h2 {
   letter-spacing: 2px;
   font-size: 2rem;
}

.languages ul {
   display: block;
}

.languages ul li {
   list-style-type: none;
   margin-bottom: 3px;
}

.about-container {
   width: 100%;
   padding: 3rem;
   height: 400px;
   display: flex;
   justify-content: center;
   background: linear-gradient(180deg, #2d3436 0%, #2d3436 35%, #636e72 100%);
}

.about-container h2 {
   font-size: 2.8rem;
   text-align: center;
   margin-bottom: 20px;
   letter-spacing: 3px;
   text-transform: uppercase;
}

.about-container p {
   font-size: 1.1rem;
   letter-spacing: 3px;
}

.about-content {
   width: 1200px;
}

nav {
   height: 60px;
   width: 100%;
   background-color: var(--primary-color);
   position: relative;
   box-shadow: 0px -5px 10px black;
}

.nav-logo {
   position: absolute;
   top: 50%;
   left: 30px;
   color: white;
   transform: translateY(-50%);
   text-decoration: none;
   font-size: 30px;
   text-transform: uppercase;
}

.nav-items {
   display: flex;
   height: 100%;
   align-items: center;
   right: 200px;
   position: absolute;
}

.nav-item {
   text-decoration: none;
   margin-right: 30px;
   font-size: 1.2rem;
   color: white;
}

.nav-item:hover {
   border-bottom: 2px solid var(--secondary-color);
   margin-bottom: -2px;
}

.social-links {
   right: 10px;
   position: absolute;
   font-size: 1.3em;
}

.social-links i {
   margin-right: 10px;
}

.active {
   color: var(--secondary-color);
}

.hero {
   width: 100%;
   display: flex;
   background-color: var(--hero-background);
   align-items: center;
   justify-content: center;
   padding: 3rem;
   color: white;
   box-shadow: -3px 0px 15px black;
}

.hero-container {
   display: flex;
   flex-direction: column;
   width: 650px;
   margin-left: 2rem;
}

.hero-container h1 {
   font-size: 3rem;
   letter-spacing: 1.5px;
}
.hero-container h2 {
   font-size: 1.7rem;
   letter-spacing: 1.5px;
   margin-bottom: 5px;
   text-transform: uppercase;
}
.hero-container p {
   font-size: 1rem;
   letter-spacing: 1px;
}

.project {
  width: 800px;
  height: 450px;
  position: relative;
  overflow: hidden;
  margin: 5px;
  box-shadow: 0px 0px 5px black;
}

.project img {
  width: 100%;
}

.project .overlay {
  z-index: 10;
  width: 100%;
  bottom: 0;
  height: 100%;
  position: absolute;
}

.overlay:hover {
  opacity: 0.7;
}

.project-info {
  width: 100%;
  height: 100%;
  top: 30%;
  padding: 2rem;
  position: absolute;
  z-index: 20;
  color: white;
}

.project-overlay {
  opacity: 0;
  transition: 0.4s ease;
}

.project-overlay:hover {
  opacity: 1;
}

.project-title {
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 3rem;
}

.project-description {
  width: 600px;
  font-size: 1.5rem;
  margin: 0 auto;
}

.language-figure {
  bottom: 0;
  position: absolute;
  z-index: 100;
  background-color: var(--secondary-color);
  left: 0;
  padding: 5px 10px;
  border-radius: 10px 10px 0px 0px;
  color: white;
  margin-left: 30px;
}

.idea1-container {
  display: flex;
  width: 100vw;
  height: 100vh;
  background-color: red;
  justify-content: space-evenly;
}

.photography {
  background-color: aquamarine;
  width: 50%;
  height: 100vh;
  transition: all 0.3s ease-out;
}

.photography-bkg,
.resume-bkg {
  height: 100%;
  overflow: hidden;
}

.resume {
  background-color: white;
  width: 50%;
  height: 100vh;
  transition: all 0.3s ease-out;
}

.resume:hover,
.photography:hover {
  transform: scale(1.01);
}

